import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { EstateComponent } from './estate.component';
import { EstateAddComponent } from './estate-add/estate-add.component';
import { EstateEditComponent } from './estate-edit/estate-edit.component';
import { EstateRoutingModule } from './estate-routing.module';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    EstateRoutingModule

  ],
  declarations: [
    EstateComponent,
    EstateAddComponent,
    EstateEditComponent
],
entryComponents:[
  EstateAddComponent,
  EstateEditComponent
],
})
export class EstateModule { }


